import React from 'react'
import './index.css'

class Selection extends React.Component {

    createOptionElementsFromList( optionsList ){
        return optionsList.map( ( option, i ) => <option value={option} key={i}>{option}</option> )
    }

    render(){
        return(
            <div className={"Selection "}>
                <select onChange={this.props.onChange}>
                    <option defaultValue={"All"}>All</option>
                    { this.createOptionElementsFromList( this.props.options ) };
                </select>
            </div>
        )
    }
}

class YearCategoryElement extends React.Component {

    render(){

        return(
            <div className="YearCategoryElement" onClick={()=>this.props.onClick(this.props.laureates)}>
                <div className="Year" >{this.props.year}</div>
                <div className='Category'>{this.props.category}</div>
            </div>
        )
    }
}

class ListOfYearCategoryElements extends React.Component {

    render(){

        let listOfYearAndCategoryElements = this.props.prizes.map( (d, i) => <YearCategoryElement {...d} onClick={this.props.onClick} key={i}/>);

        return (
            <div className="listOfYearCategoryElements">
                {listOfYearAndCategoryElements}
            </div>
        );
    }
}

let ListOfLaureates = ( data ) => {

    return (
        <div>
            <div>
                {data.laureates.map( (d, i) => {
                    return(
                        <div className='laureat' key={i}>
                            <div className='name'>
                                <div className="firstname">{d.firstname}</div>
                                <div className="surname">{d.surname}</div>
                            </div>
                            <div className="motivation">{d.motivation}</div>
                        </div>
                    )
                })}
            </div>
        </div>
    )
};


class App extends React.Component {

    constructor(props){

        super(props);
        this.changeShowedInfo = (laureates ) => this.setState({laureates: laureates});
    }

    state = {

        prizes: this.props.prizes,
        laureates: this.props.prizes[0].laureates,
        yearForFilter: "All",
        categoryForFilter: "All",

    };

    Filter = ( prizes, year, category) => {
        return prizes.filter( d => year === "All" ? true : d.year === year )
            .filter( d => category === "All" ? true : d.category === category )
    };

    getUniquePropertyFromObj = ( listOfObjects, property ) => {
       return listOfObjects
           .map( obj => obj[property])
           .reduce( (newArray, element) => {
              if( !newArray.includes( element) ){
                  newArray.push( element )
              }
              return newArray
           }, [])
    };

    render(){

        let listOfUniqueYear = this.getUniquePropertyFromObj(this.props.prizes, 'year');
        let listOfUciqueCategory = this.getUniquePropertyFromObj(this.props.prizes, 'category');
        let filteredPrizes = this.Filter(this.state.prizes, this.state.yearForFilter, this.state.categoryForFilter);

        return (
            <div className="App">
                <div className="header">
                    <div className={'filters'}>
                        <div>filters</div>
                        <Selection options={ listOfUniqueYear }
                                   onChange={(e)=> this.setState({yearForFilter: e.target.value})}/>
                        <Selection options={ listOfUciqueCategory }
                                    onChange={(e)=>this.setState( {categoryForFilter: e.target.value})
                                    }/>
                    </div>
                </div>
                <div className="Laureates">

                    <ListOfYearCategoryElements prizes={ filteredPrizes }
                                                onClick={( laureates ) => this.changeShowedInfo( laureates )}/>

                    <div className="TableInfo">
                        <h1 className={'title'}>Лауреаты Нобелевской премии</h1>
                        <div>
                            <ListOfLaureates laureates={this.state.laureates}/>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default App
